
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'

import Progress from 'c%/progress'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

export default{
mixins: [ common, globals ],
components: {
Tab,
Swiper,
TabItem,
Progress,
SwiperItem
},
data () {
return {
list: ['在读课程','已读课程','计划课程'],
tab: 0,
data_uri: 'lessons?is_learning=0',
data: {0: [],1: [],2: []}
}
},
watch: {
tab: function (val) {
this.data_uri = 'lessons?is_learning='+val
}
},
methods: {
getLessonPercent (row) {
return parseFloat(parseFloat(row.remain_lesson_hours) / parseFloat(row.lesson_hours)).toFixed(2) * 100
},
deal_data (response) {
let list = response.body.data.list,
total = response.body.data.total,
tab = this.tab
if(this.pageIndex===1){
this.data[tab] = []
}

if(list.length>0){
this.data[tab] = this.data[tab].concat(list)
}

this.mescroll.endBySize(list.length,total)
}
},
computed: {
swiperHeight () {
let height = window.innerHeight
return (height - this.headerHeight - 44) +'px'
}
}

}



